import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { retry, catchError, tap, switchMap, map } from "rxjs/operators";
import { ObservableInput, BehaviorSubject, forkJoin } from "rxjs";

// const urlGlobalBase = "https://thevirustracker.com/free-api?countryTimeline=";
const urlGlobalBase = "https://coronavirus-tracker-api.herokuapp.com/v2/locations/"
const urlLocationCountries = "https://coronavirus-tracker-api.herokuapp.com/v2/locations"

@Injectable({
  providedIn: "root",
})
export class GlobalDataService {
  constructor(private http: HttpClient) {}

  // requestCountryData(countryCode: string) {
  requestLocationData(locationId: number) {
    return this.http
      .get<any>(this.getUrlGlobal(locationId))
      .pipe(
        retry(3), // retry a failed request up to 3 times
        catchError(this.handleError) // then handle the error
      )
  }

  requestContryLocationId(countryCode: string) {
    return this.http
      .get<any>(`${urlLocationCountries}?country_code=${countryCode}`)
      .pipe(
        retry(3), // retry a failed request up to 3 times
        catchError(this.handleError) // then handle the error
      )
  }

  requestCountryData(countrycode: string) {
    return this.requestContryLocationId(countrycode).pipe(
      map(resp => {
        console.log('switched');
        return resp.locations[0].id
      }),
      switchMap((idLocation: any) => {
        return this.requestLocationData(idLocation)
      })
    )
  }

  // getUrlGlobal(countryCode: string) {
    getUrlGlobal(countryCode: number) {
    return `${urlGlobalBase}${countryCode}`;
    // return `${urlGlobalBase}`;
  }

  handleError(error: any): ObservableInput<any> {
    console.log("error");
    return;
  }

  // Store
  private readonly countriesDataSubjst = new BehaviorSubject<any[]>([]);
  readonly countriesData$ = this.countriesDataSubjst.asObservable();

  private get countriesData(): any[] {
    return this.countriesDataSubjst.getValue();
  }

  private set countriesData(val: any[]) {
    this.countriesDataSubjst.next(val);
  }

  public triggerCountriesDataSubject() {
    this.countriesDataSubjst.next(this.countriesData)
  }


  /*
  Obs: metric is not being used here... it provides all the data
  */
  addCountryData({countryCodes, metric}: {countryCodes: string[], metric: string}) {
    // const codes = [39, 40]
    const observables = countryCodes.map((code) =>
      this.requestCountryData(code)
    );

    forkJoin(observables)
      .pipe(
        tap((array) => {
          this.countriesData = array;
        })
      )
      .subscribe((resp) => {
        console.log("resp!");
      });
    // const newCountryData
    // this.countriesData = [
    //   ...this.countriesData,
    //   newCountryData
    // ]
  }
}
