import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { PageGlobalComponent } from './pages/page-global/page-global.component';
import { PageBrazilComponent } from './page-brazil/page-brazil.component';
import { PageTimeSeriesComponent } from './page-time-series/page-time-series.component';
import { PageBrazilMapComponent } from './page-brazil-map/page-brazil-map.component';


const routes: Routes = [
  {path: '', component: PageGlobalComponent},
  {path: 'timeseries', component: PageTimeSeriesComponent},
  {path: 'brazil-map', component: PageBrazilMapComponent},
  {path: 'analyzes', component: PageBrazilComponent},
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
