import { Component, OnInit } from "@angular/core";
import { GlobalDataService } from "src/app/services/global-data.service";
import { map, filter, startWith, debounceTime, switchMap, delay, take, shareReplay } from "rxjs/operators";
import { countriesReversed } from "src/app/data/countries";
import { FormControl } from "@angular/forms";
import mapFullDataset from "./outmap.json";
import { of, Subject, combineLatest, BehaviorSubject, interval, Subscription } from "rxjs";
import { MatSliderChange } from '@angular/material/slider';
import { BreakpointObserver, Breakpoints } from '@angular/cdk/layout';

@Component({
  selector: "app-page-global",
  templateUrl: "./page-global.component.html",
  styleUrls: ["./page-global.component.scss"],
})
export class PageGlobalComponent implements OnInit {

  metricCtrl = new FormControl("confirmed");
  typeCtrl = new FormControl("cummulative");
  normalizeInitialCtrl = new FormControl(0);
  countryCodes: string[];
  isLine = true;
  allTimes: string[]

  isSmallScreen = this.breakpointObserver.observe(Breakpoints.Handset).pipe(
    map(result => result.matches),
    shareReplay()
  );

  mapGraphSbj = new BehaviorSubject<any>(null)
  mapGraphSubj$ = this.mapGraphSbj.asObservable()

  constructor(
    public globalSvr: GlobalDataService,
    private breakpointObserver: BreakpointObserver,
  ) {
  }


  sliderSubject = new Subject()

  readonly mapGraph$ = this.mapGraphSubj$.pipe(
      delay(20),
    map((a) => {
      const mapDatase = mapFullDataset
      console.log("IN MAP GRAPH PIPE");
      const longitude = mapDatase.locations.map(
        (loc) => loc.coordinates.longitude
      )
      const latitude = mapDatase.locations.map(
        (loc) => loc.coordinates.latitude
      );
      this.allTimes = Object.keys(mapDatase.locations[0].timelines.confirmed.timeline).sort()

      const cases = mapDatase.locations.map(
        (loc) => loc.timelines.confirmed.timeline[this.allTimes[a.index]]
      );
      const text = mapDatase.locations.map((loc, index) => `${loc.country} - ${loc.province} \n ${cases[index]}`)

      var idxCut = longitude.map((e, i) => e != "0.0" ? i : '').filter(String)

      const cutLon = idxCut.map(index => longitude[index])
      const cutLat = idxCut.map(index => latitude[index])
      const cutText = idxCut.map(index => text[index])
      const cutCases = idxCut.map(index => cases[index])
      const data = [
        {
          type: "scattergeo",
          mode: "markers",
          lon: cutLon,
          lat: cutLat,
          marker: {
            size: cutCases.map(v =>  v > 0 ? (Math.log10(v) - 0)/(5 - 0)*10 : 0),
            // color: [10, 20, 40, 50],
            cmin: 0,
            cmax: 10,
            line: {
              color: "blue",
            },
          },
          // z: latestCases,
          text: cutText,
          hoverinfo:'text'
          // autocolorscale: true,
        },
      ];

      const layout = {
        title: "Covid Spread (logarithm scale)",
        margin: {
          l: 0,
          r: 0,
          b: 0,
        },
      };
      return {
        data,
        layout: layout,
      };
    })
  );

  ngOnInit(): void {
    this.mapGraphSbj.next({index: 0})

  }

  handleSliderChanged(event: MatSliderChange) {
    console.log('slider changed');
    this.mapGraphSbj.next({index: event.value})
  }

  playSubcription: Subscription
  handlePlay() {
    const a = 1
    const counters = interval(300).pipe(take(this.allTimes.length));
    this.playSubcription = counters.subscribe(count => {
      this.valueSlider = count
      this.mapGraphSbj.next({index: count})
    })
  }

  handlePause() {
    this.playSubcription.unsubscribe()
  }

  formatLabel(value: number) {
    // console.log(this.allTimes)
    // return this.allTimes[value]
    // if (value >= 1000) {
    //   return Math.round(value / 1000) + 'k';
    // }
    return value;
  }
  valueSlider: number = 0
}

function unpack(rows, key) {
  return rows.map(function (row) {
    return row[key];
  });
}
