import { COMMA, ENTER } from "@angular/cdk/keycodes";
import { Component, OnInit, ElementRef, ViewChild, Output, EventEmitter } from "@angular/core";
import { FormControl } from "@angular/forms";
import { Observable } from "rxjs";
import {
  MatAutocompleteSelectedEvent,
  MatAutocomplete,
} from "@angular/material/autocomplete";
import { MatChipInputEvent } from '@angular/material/chips';
import { startWith, map } from 'rxjs/operators';
import {countryNames} from '../data/countries'

@Component({
  selector: "app-chips-autocomplete",
  templateUrl: "./chips-autocomplete.component.html",
  styleUrls: ["./chips-autocomplete.component.scss"],
})
export class ChipsAutocompleteComponent implements OnInit {
  visible = true;
  selectable = true;
  removable = true;
  separatorKeysCodes: number[] = [ENTER, COMMA];
  fruitCtrl = new FormControl();
  filteredCountries: Observable<string[]>;
  countries: string[] = ["Brazil", 'United States', 'Italy', 'Spain'];
  allCountries: string[] = countryNames

  @Output() onSelectionChange = new EventEmitter<string[]>()

  @ViewChild("fruitInput", { static: true }) fruitInput: ElementRef<
    HTMLInputElement
  >;
  @ViewChild("auto", { static: true }) matAutocomplete: MatAutocomplete;

  constructor() {
    this.filteredCountries = this.fruitCtrl.valueChanges.pipe(
      startWith(null),
      map((fruit: string | null) =>
        fruit ? this._filter(fruit) : this.allCountries.slice()
      )
    );
  }

  add(event: MatChipInputEvent): void {
    const input = event.input;
    const value = event.value;

    // Add our fruit
    if ((value || "").trim()) {
      this.countries.push(value.trim());
    }

    // Reset the input value
    if (input) {
      input.value = "";
    }

    this.fruitCtrl.setValue(null);
  }

  remove(fruit: string): void {
    const index = this.countries.indexOf(fruit);

    if (index >= 0) {
      this.countries.splice(index, 1);
    }
    this.onSelectionChange.emit(this.countries)
  }

  selected(event: MatAutocompleteSelectedEvent): void {
    this.countries.push(event.option.viewValue);
    this.fruitInput.nativeElement.value = "";
    this.fruitCtrl.setValue(null);
    this.onSelectionChange.emit(this.countries)
  }

  private _filter(value: string): string[] {
    const filterValue = value.toLowerCase();

    return this.allCountries.filter(
      (fruit) => fruit.toLowerCase().indexOf(filterValue) === 0
    );
  }

  ngOnInit(): void {
    this.onSelectionChange.emit(this.countries)
  }
}
